.App {
  text-align: center;
  width: 100vw;
  height: 100vh;
  position: relative;
}

.App-footer {
  height: 8vh;
  width: 100%;
  position: absolute;
  left: 0;
  bottom: 0;
  background-color: rgba(255,255,255,0.53);
}

.img-title {
  width: 12vw;
  min-height: 15vh;
  position: absolute;
  left: 6vw;
  top: 3vh;
  z-index: 3;
}

.img-btn {
  width: 10.2vw;
  height: 25.6vw;
  position: absolute;
  left: 6vw;
  bottom: 1vh;
  z-index: 1;
}

.play-btn {
  width: 10vw;
  height: 4.4vh;
  position: absolute;
  right: 6vw;
  top: 3vh;
}

.navbar {
  position: absolute;
  right: 0;
  bottom: 1.5vh;
  z-index: 1;
}

.icon-btn {
  max-width: 12vw;
  max-height: 11vh;
  margin-right: 3vw;
}

.modal-container {
  position: absolute;
  left: 0;
  bottom: 0;
  height: 85vh;
  width: 100vw;
  z-index: 2;
}

.modal-content {
  margin: 18vw 5.6vw;
  border: 1vw rgba(255,255,255,0.53) solid;
  width: 88.8vw;
}

.text-content {
  font-size: 1.1em;
  color: #fff;
  text-align: left;
  margin: 0 4vw;
}

.model-content {
  min-width: 100vw;
  min-height: 100vh;
  padding-top: -10vw;
}

.modal-loading {
  position: absolute;
  left: 29vw;
  top: 42vw;
  width: 42vw;
  height: 42vw;
  z-index: 2;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.loading-content {
  color: #737373;
  font-size: 1.2em;
}


